// FONTS
@font-face {
  font-family: "AvenirLTPro-Book";
  src: url("/public/fonts/AvenirLTPro-Book/font.woff2") format("woff2"),
    url("/public/fonts/AvenirLTPro-Book/font.woff") format("woff");
  font-display: fallback;
}

@font-face {
  font-family: "AvenirLTPro-Heavy";
  src: url("/public/fonts/AvenirLTPro-Heavy/font.woff2") format("woff2"),
    url("/public/fonts/AvenirLTPro-Heavy/font.woff") format("woff");
  font-display: fallback;
}

@font-face {
  font-family: "AvenirLTPro-Medium";
  src: url("/public/fonts/AvenirLTPro-Medium/font.woff2") format("woff2"),
    url("/public/fonts/AvenirLTPro-Medium/font.woff") format("woff");
  font-display: fallback;
}

// FONTS

// GLOBAL SCSS

html {
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  @media (min-width: 768px) {
    font-size: 18px;
  }
}

body {
  background-color: var(--body-bg-color);
  font-family: var(--font-family-light);
  margin: 0;
  transition: color 0.05s ease-in-out;
}

:root {
  /* Colours */
  --primary-400: #16697a;
  --primary-500: #0f4d59;
  --primary-600: #0b3a44;
  --primary-200: #d3ecf1;
  --secondary-200: #92d2d2;
  --secondary-400: #59c3c3;
  --secondary-500: #53b6b6;
  --gray-200: #e5e7eb;
  --gray-300: #d1d5db;
  --gray-400: #9ca3af;
  --gray-500: #6b7280;
  --gray-700: #374151;
  --fiery-rose: #fc6471;
  --heliotrope-gray: #aea4bf;
  --wheat-200: #e1ca96;
  --wheat: #efeadd;
  --white-50: #fefefe;
  --white: #ffffff;
  --twitter-pastel: #67c3fc;
  /* Semantic color names */
  --logo-color: var(--primary-400);
  --title-color: var(--primary-400);
  --post-card-bg: var(--white);
  --tag-btn-bg: var(--white);
  --tag-btn-bg-hover: var(--primary-400);
  --tag-btn-bg-hover-text: var(--white);
  --footer-bg: var(--primary-200);
  --body-text-color: var(--gray-700);
  --link-hover-color: var(--primary-400);
  --body-bg-color: var(--white-50);
  --subtitle-text-color: var(--gray-500);
  --theme-hover-color: var(--primary-400);
  --search-bar-bg: var(--white-50);
  --search-bar-border: var(--gray-300);
  --search-bar-placeholder: var(--gray-400);
  --heart-accent-color: var(--primary-400);
  --alert-bg: #fdecc6;
  --navbar-bg: var(--primary-200);
  /* // Font sizes */
  --big-header: 4.209rem;
  --heading-one: 3.157rem;
  --heading-two: 2.369rem;
  --heading-three: 1.777rem;
  --heading-four: 1.333rem;
  --small-text: 0.88rem;
  --alert-text: 1.2rem;
  --body-text: 1rem;
  /* // Font families */
  --font-family-heavy: "AvenirLTPro-Heavy", sans-serif, system-ui;
  --font-family-medium: "AvenirLTPro-Medium", sans-serif, system-ui;
  --font-family-light: "AvenirLTPro-Book", sans-serif, system-ui;
  /* // Widths */
  --twelve-column-width: 55rem;
}

[data-theme="dark"] {
  --body-text-color: var(--white-50);
  --link-hover-color: var(--secondary-500);
  --logo-color: var(--primary-400);
  --title-color: var(--white-50);
  --post-card-bg: var(--primary-400);
  --tag-btn-bg: var(--primary-400);
  --tag-btn-bg-hover: var(--primary-200);
  --tag-btn-bg-hover-text: var(--gray-700);
  --footer-bg: var(--primary-500);
  --body-bg-color: var(--primary-600);
  --subtitle-text-color: var(--gray-200);
  --theme-hover-color: var(--secondary-400);
  --search-bar-bg: var(--primary-500);
  --search-bar-border: var(--secondary-200);
  --search-bar-placeholder: var(--white);
  --heart-accent-color: var(--secondary-400);
  --navbar-bg: var();
}

.title {
  font-family: var(--font-family-heavy);
  font-size: var(--heading-one);
  color: var(--title-color);

  @media (min-width: 768px) {
    font-size: var(--big-header);
  }
}

h1,
h2,
h3,
h4 {
  color: var(--body-text-color);
  font-weight: normal;
  margin: 0 0 1.38rem;
}

p,
li {
  color: var(--body-text-color);
  margin-bottom: 1rem;
  line-height: 1.75;
}

.h1,
.h2,
.h3,
.h4 {
  font-family: var(--font-family-medium);
}

.h1 {
  font-size: var(--heading-one);
}

.h2 {
  font-size: var(--heading-two);
}

.h3 {
  font-size: var(--heading-three);
}

.h4 {
  font-size: var(--heading-four);
}

ul,
ol {
  color: var(--body-text-color);
}

//
nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 26px;
  border-radius: 0px 0px 15px 15px;
  background-color: var(--footer-bg);
  @media (min-width: 768px) {
    margin-bottom: 48px;
  }
}

.theme-changer {
  cursor: pointer;
  border: none;
  background: none;
  margin-right: -8px;

  .theme-icon {
    fill: var(--body-text-color);
    transition: fill 0.1s ease-in-out;
  }

  &:hover {
    cursor: pointer;
    color: var(--tag-btn-bg-hover-text);
    background-color: unset !important;
  }
}

.nav-links {
  display: flex;
  align-items: center;
  gap: 16px;

  @media (min-width: 768px) {
    gap: 24px;
  }

  a {
    color: var(--body-text-color);
    font-family: var(--font-family-medium);
    display: inline-block;
    text-decoration: none;
    position: relative;

    &:before {
      content: "";
      background: var(--secondary-400);
      width: 100%;
      height: 2px;
      position: absolute;
      bottom: -4px;
      opacity: 0;
      transition: opacity 0.1s ease-in-out;
    }

    &:hover::before {
      opacity: 1;
    }
  }

  .active {
    font-family: var(--font-family-heavy);
    position: relative;

    &:before {
      content: "";
      background: var(--secondary-400);
      width: 100%;
      height: 2px;
      position: absolute;
      bottom: -4px;
      opacity: 1;
    }
  }
}

.logo-link:hover {
  animation: rattle 400ms ease-in-out 1;
}

//

.prose {
  display: flex;
  flex-direction: column;
  max-width: 820px;
  width: 100%;
  white-space: pre-wrap;
  color: var(--body-text-color);
}

h2,
h3,
h4 {
  display: flex;
  align-items: center;

  a {
    width: 24px;
  }
}

img {
  // TODO: Responsive styles
  max-width: 100%;
  width: 100%;
  margin: 0 auto;

  @media (min-width: 768px) {
    max-width: 120%;
    width: 120%;
    margin: 0 -10%;
  }
}

.anchor {
  padding-right: 6px;
  margin-left: -30px;

  .jam-link {
    fill: var(--body-text-color);
    visibility: hidden;
  }
}

h2 {
  font-size: var(--heading-two);

  @media (min-width: 768px) {
    font-size: var(--heading-one);
  }
}

h3 {
  font-size: var(--heading-three);

  @media (min-width: 768px) {
    font-size: var(--heading-two);
  }
}

h4 {
  font-size: var(--heading-four);

  @media (min-width: 768px) {
    font-size: var(--heading-three);
  }
}

h2:hover > .anchor .jam-link,
h3:hover > .anchor .jam-link,
h4:hover > .anchor .jam-link {
  visibility: visible;
}

blockquote {
  border-left: 5px solid var(--heliotrope-gray);
  margin-inline-start: 0;
  padding-left: 40px;
  font-size: 20px;
  font-style: italic;
}

p,
ul,
ol {
  margin-top: 0;
}

li {
  margin-bottom: 8px;
}

hr {
  width: 100%;
  border: 1px solid var(--gray-200);
}

.visually-hidden {
  clip: rect(1px, 1px, 1px, 1px);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}

.visually-hidden:focus {
  clip: auto;
  height: auto;
  overflow: auto;
  position: absolute;
  width: auto;
}

/* // Uses Prism Monokai Dark Styles */
code[class*="language-"],
pre[class*="language-"],
pre {
  font-size: 0.9rem;
  line-height: 1.5;
  direction: ltr;
  text-align: left;
  white-space: pre;
  word-spacing: normal;
  word-break: normal;
  -moz-tab-size: 2;
  -o-tab-size: 2;
  tab-size: 2;
  -webkit-hyphens: none;
  -moz-hyphens: none;
  -ms-hyphens: none;
  hyphens: none;
  background: #272822;
  color: #f8f8f2;
}

pre[class*="language-"],
pre {
  padding: 1rem 1rem;
  margin: 0.5em 0;
  border-radius: 5px;
  overflow: auto;
}

pre code[class*="language-"] {
  padding: 0;
}

.highlight-line {
  display: block;
  padding: 0.125em 1em;
  text-decoration: none;
  /* override del, ins, mark defaults */
  color: inherit;
  /* override del, ins, mark defaults */
}

pre,
code {
  font-family: Consolas, Menlo, Monaco, "Andale Mono WT", "Andale Mono",
    "Lucida Console", "Lucida Sans Typewriter", "DejaVu Sans Mono",
    "Bitstream Vera Sans Mono", "Liberation Mono", "Nimbus Mono L",
    "Courier New", Courier, monospace;
  line-height: 1.5;
  border-radius: 5px;
}

code {
  background-color: var(--gray-200);
  color: var(--gray-700);
  font-size: 0.8rem;
  padding: 0.2rem 0.5rem;
}

@media screen and (prefers-reduced-motion: reduce) {
  * {
    /* Very short durations means JavaScript that relies on events still works */
    animation-duration: 0.001ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.001ms !important;
  }
}
.alert-box {
  display: flex;
  padding: 16px;
  background: var(--alert-bg);
  gap: 16px;
  box-shadow: 0px 4px 10px -4px rgba(0, 0, 0, 0.15);
  margin-bottom: 16px;
  justify-content: center;
  @media (min-width: 768px) {
    margin-bottom: 32px;
    padding: 32px 0;
  }
}

.alert-icon {
  font-size: var(--heading-four);
  font-family: "Noto Color Emoji", "Apple Color Emoji", "Segoe UI Emoji", Times,
    Symbola, Aegyptus, Code2000, Code2001, Code2002, Musica, serif, LastResort;
}

// p {
//   margin: 0;
//   color: var(--gray-700);
// }

.alert-title {
  color: var(--gray-700);
  font-size: var(--alert-text);
  margin-bottom: 8px;
  margin-top: 2px; // to align with the alert-icon
}

footer {
  margin-top: 44px;
  display: flex;
  flex-direction: column;
  background-color: var(--footer-bg);
  justify-content: flex-end;
  padding: 40px 0 20px;

  @media (min-width: 768px) {
    margin-top: 88px;
  }
}
span {
  color: var(--heart-accent-color);
}

p {
  text-align: center;
  margin: 0 0 8px;
}

:global(.footer-icon) {
  fill: var(--body-text-color);
}

a {
  display: inline-block;
  margin-right: 16px;
  opacity: 1;
  transition: opacity 0.02s;

  &:hover {
    opacity: 0.7;
  }
}
a:last-child {
  margin-right: 0;
}

.container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  justify-content: space-between;
  padding: 8px;

  @media (min-width: 835px) {
    padding: 0;
  }
}

.main-content {
  max-width: var(--twelve-column-width);
  width: 100%;
  margin: 0 auto;
}
.footer-container {
  margin: -8px;
  @media (min-width: 835px) {
    margin: 0;
  }
}
.subtitle {
  font-family: var(--font-family-light);
  max-width: 820px;
  margin-bottom: 56px;
}

a {
  color: var(--body-text-color);
  text-decoration: none;
  &:visited {
    color: var(--body-text-color);
    text-decoration: none;
  }
}

.header-links {
  font-family: var(--font-family-heavy);
  position: relative;
  display: inline-block;
  span {
    position: absolute;
    top: 0;
    left: 0;
    overflow: hidden;
    transform: translateX(-100%);
    transition: transform 200ms ease;

    &:before {
      display: inline-block;
      content: attr(data-content);
      color: var(--secondary-400);
      transform: translateX(100%);
      transition: transform 200ms ease;
    }
  }

  &:hover {
    span {
      transform: translateX(0);
    }

    span::before {
      transform: translateX(0);
    }
  }
}

.yt-link {
  span::before {
    color: var(--fiery-rose);
  }
}

.post-cards {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(min(230px, 100%), 1fr));
  grid-gap: 20px;
  margin-bottom: 16px;

  @media (min-width: 768px) {
    grid-gap: 30px;
  }
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.post-card {
  border-radius: 5px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  position: relative;
  background: linear-gradient(
    -45deg,
    var(--secondary-200),
    var(--heliotrope-gray),
    var(--fiery-rose)
  );
  background-size: 300% 300%;
  height: 160px;

  @media (min-width: 768px) {
    height: 200px;
  }
}

.post-card-inner {
  background: var(--post-card-bg);
  border: 2px solid var(--primary-400);
  border-radius: 5px;
  display: flex;
  position: relative;
  height: 100%;
  flex-direction: column;
  text-decoration: none;
  transition: transform 0.1s ease-in-out;
}

.post-card:hover .post-card-inner {
  transform: translate(-5px, -10px);

  @media (min-width: 768px) {
    transform: translate(-8px, -15px);
  }
}

.post-card:active .post-card-inner {
  transform: translate(-3px, -5px);
}

.post-card:hover {
  animation: gradient 3s ease infinite;
}

.post-card-title {
  font-family: var(--font-family-medium);
  color: var(--body-text-color);
  text-decoration: none;
}

.post-card-info {
  color: var(--subtitle-text-color);
  font-size: var(--small-text);
}

.post-card-info,
.post-card-title {
  margin: 16px 16px 0;
}

.post-card-tags {
  position: absolute;
  bottom: 16px;
  right: 16px;
}
.more-posts-container {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  text-align: right;
}

.more-posts-link {
  position: relative;
  font-family: var(--font-family-medium);
  display: flex;
  align-items: center;
  color: var(--body-text-color);
  :global(.homepage-arrow) {
    fill: var(--body-text-color);
  }
  &:hover > span::before {
    opacity: 1;
  }
  span {
    position: relative;
    &:before {
      content: "";
      background: var(--secondary-400);
      width: 100%;
      height: 2px;
      position: absolute;
      bottom: -1px;
      opacity: 0;
      transition: opacity 0.1s ease-in-out;
    }
  }
}
.post-cards {
  margin-top: 48px;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(min(230px, 100%), 1fr));
  grid-gap: 20px;
  margin-bottom: 16px;

  @media (min-width: 768px) {
    grid-gap: 30px;
  }
}

.searchbar {
  position: relative;
  display: flex;
  margin-bottom: 10px;
}

.searchbar-icon-container {
  position: absolute;
  height: 100%;
  left: 15px;
  display: flex;
  align-items: center;

  :global(.search-icon) {
    fill: var(--search-bar-placeholder);
  }
}

input {
  color: var(--body-text-color);
  background-color: var(--search-bar-bg);
  font-family: var(--font-family-light);
  font-size: 1rem;
  width: 100%;
  height: 48px;
  padding-top: 5px;
  border-radius: 5px;
  border: 2px solid var(--search-bar-border);
  position: relative;
  padding-left: 40px;

  &::placeholder {
    color: var(--search-bar-placeholder);
    font-size: 1rem;
    font-family: var(--font-family-light);
  }
}
ul {
  display: flex;
  list-style: none;
  padding: 0;
  flex-wrap: wrap;
  gap: 8px;
}

li {
  margin: 0;
}
button {
  color: var(--body-text-color);
  font-family: var(--font-family-light);
  font-size: var(--small-text);
  padding: 8px;
  background: var(--tag-btn-bg);
  border-radius: 5px;
  border: 1px solid var(--primary-400);
  text-transform: capitalize;
  transition: color 0.1s ease-in-out, background-color 0.08s ease-in-out;

  &:hover {
    cursor: pointer;
    color: var(--tag-btn-bg-hover-text);
    background: var(--tag-btn-bg-hover);
  }
}
button.selected {
  color: var(--tag-btn-bg-hover-text);
  background: var(--tag-btn-bg-hover);
}
a {
  text-decoration: none;
  color: var(--body-text-color);
  font-family: var(--font-family-heavy);
  transition: color 0.05s ease-in;

  &:hover {
    color: var(--link-hover-color);
  }
}

p {
  margin-top: 0;
}

.row {
  display: flex;
  flex-direction: column;

  @media (min-width: 768px) {
    flex-direction: row;
  }
}

.profile-img__container {
  width: 325px;
  background-color: var(--primary-200);
  border-radius: 5px;
  img {
    transform: translate(16px, -10px);
    border-radius: 5px;
    max-width: 100%;
  }
}

.col:first-child {
  margin-bottom: 16px;
  margin-right: 16px;

  @media (min-width: 768px) {
    margin-right: 16px;
  }
}
.col:last-child {
  @media (min-width: 768px) {
    margin-left: 16px;
  }

  &.first-section {
    @media (min-width: 768px) {
      margin-left: 32px;
    }
  }
}

.home-description {
  text-align: center;
  color: var(--body-text-color);
}

.contact-cards {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  padding: 50px;
}

.contact-card {
  p {
    color: var(--body-bg-color);
    padding-top: 5px;
  }
  svg {
    color: var(--body-bg-color);
  }
  flex-direction: column;
  background-color: var(--title-color);
  width: 150px;
  height: 150px;
  border-radius: 100px;
  justify-content: center;
  display: flex;
  align-items: center;
}

.about-container {
  p {
    color: var(--body-text-color);
  }
}

.post-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  p {
    margin: unset;
    padding: unset;
    color: var(--body-text-color);
  }
}

.service-cards {
  display: flex;
  flex-direction: column;
  border: "10px solid darkgreen";
  img {
    height: 120px;
    width: 120px;
  }
}

.service-card {
  display: flex;
  flex-direction: column;
  border: 1px solid var(--title-color);
  border-radius: 20px;
  padding: 20px;
  margin: 10px;
  p {
    color: var(--body-text-color);
  }
  h3 {
    margin: 0;
  }
  svg {
    color: var(--body-text-color);
  }
}
.service-card-title {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.service-card-title * {
  margin: 0 10px;
}
